	module.exports = {"data":{"post":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2018-07-07--Sweet-and-Delicate/index.md absPath of file >>> MarkdownRemark","html":"<h2>Sweet And Delicate</h2>\n<p>It’s been a while since I wanted to do a portrait with a different mood and feeling from my usual works.</p>\n<p>Also I did this to study female anatomy and try some new composition techniques.</p>\n<h3>Design And Inspiration</h3>\n<p>I wanted her to kinda feel -as the title says- “sweet and delicate.” Usually before this, I used to create some rough male characters. I wanted something different to try to expand my area of work.</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/sweet_and_delicate_by_hossimo_2-57ff345914de6e20d493f073e86616db-222c6.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\" alt=\"sweet_and_delicate_by_hossimo_2\" title=\"\" src=\"/static/sweet_and_delicate_by_hossimo_2-57ff345914de6e20d493f073e86616db-78f2b.jpg\" srcset=\"/static/sweet_and_delicate_by_hossimo_2-57ff345914de6e20d493f073e86616db-dce19.jpg 200w,\n/static/sweet_and_delicate_by_hossimo_2-57ff345914de6e20d493f073e86616db-c1413.jpg 400w,\n/static/sweet_and_delicate_by_hossimo_2-57ff345914de6e20d493f073e86616db-78f2b.jpg 800w,\n/static/sweet_and_delicate_by_hossimo_2-57ff345914de6e20d493f073e86616db-222c6.jpg 900w\" sizes=\"(max-width: 800px) 100vw, 800px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>Originally, this was inspired by my “sweetie,” and all the feelings she pumped me with. I thank her from here! <em>xo xo</em></p>\n<h3>Sculpting and Posing Process</h3>\n<p>I created a base mesh in Maya, and did the UV’s there. Then I sculpted all the details in ZBrush in M-Pose. After that I sent my low res mesh to Maya and Rigged it there. Then it was time to bring it back to ZBrush and project the details back and continue sculpting.</p>\n<p>I rendered it using ZBrush. Just created a couple of renderpasses and composed them in Photoshop.</p>\n<p>For higher resolution images, please visit my Artstation.</p>","fields":{"slug":"/Sweet-and-Delicate/","prefix":"portfolio/2018-07-07"},"frontmatter":{"title":"Sweet And Delicate","author":"Hossein Moayed","category":"portfolio","date":"2018-07-07T00:00:00.000Z","cover":{"childImageSharp":{"resize":{"src":"/static/sweet_and_delicate_by_hossimo_1-9a3e0acbee9ffa9413221e69ef0c6e83-ada8c.jpg"}}}}},"authornote":{"id":"/home/travis/build/h0ssimo/beta/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p>To make it short, I’m a 3D Character Artist experienced in character pipelines for both animations and games.</p>"},"site":{"siteMetadata":{"facebook":{"appId":"298710090861252"}}}},"pathContext":{"slug":"/Sweet-and-Delicate/","prev":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2018-07-01--Minotaur-Action-Figure/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/Minotaur-Action-Figure/","prefix":"portfolio/2018-07-01"},"frontmatter":{"title":"The Minotaur Action Figure","category":"portfolio","tags":["fantasy","sculpture"],"date":"2018-07-01T00:00:00.000Z"}},"next":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2018-08-14--Russell-Crowe/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/Russell-Crowe/","prefix":"portfolio/2018-08-14"},"frontmatter":{"title":"Gladiator Tribute","category":"portfolio","tags":["realistic","likeness"],"date":"2018-08-14T00:00:00.000Z"}}}}

